body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

select {
  appearance: none;
  background-color: transparent;
  border: none;
  padding: 0 1em 0 0;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  outline: none;
}
.arrow-button {
  background-color: #fff;
  height: 2cm;
  width: 2cm;
}
.arrow-button1 {
  background-color: #fff;
  height: 1cm;
  width: 1cm;
}
.md {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 30%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, 50%);
}
.fle {
  display: flex; 
  justify-content: space-between 
}
.mm {
  width: 100%;
  height: 100%;
}
table {width: 80%; border-collapse: collapse;}
table td {padding: 12px 16px; width: 100%;}
table thead tr { border-top: 1px solid #e8e9eb; width: 100%;}
table tbody tr:hover {background: #e8f6ff;}
.arrow {
  height: 2cm;
  width: 2cm;
  background-color: #fff;
}
.arrow1 {
  height: 1cm;
  width: 1cm;
  background-color: #fff;
}
.menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 35%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, 50%);  
}
.button1 {
  text-decoration: none;
  display: inline-block;
  color: white;
  padding: 20px 30px;
  margin: 10px 20px;
  border-radius: 10px;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  background-image: linear-gradient(to right, #9EEFE1 0%, #4830F0 51%, #9EEFE1 100%);
  background-size: 200% auto;
  box-shadow: 0 0 20px rgba(0, 0, 0, .1);
  transition: .5s;
}
.button1:hover {
  background-position: right center;
}
.equal-button {
  background-color: #fff;
  border-color: #fff;
  margin-right: 40px;
}
.equal-sign {
  height: 40px;
  width: 40px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.main-div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 35%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, 50%);
}
.item {
  margin-right: 3em;
}

.main {
  display: flex;
  flex-direction: row;
}
.item input {
  width: 300px;
  font-size: larger;
  font-family: GraphikCy-Semibold,'Helvetica CY',Arial,sans-serif;
	font-size: 13px;
	padding: 6px 0 4px 10px;
	border: 1px solid #cecece;
	background: #F6F6f6;
	border-radius: 8px;
}